/* footer section css start */
.footer-section {
  @extend %base-color-two;
  &__top {
    padding-top: rem(100px);
    padding-bottom: rem(80px); }
  p, li, span, i, a {
    color: rgba(#fff, 0.9); }
  &__bottom {
    padding: 15px 0;
    border-top: 1px solid rgba(#fff, 0.15); } }
.footer-logo {
  img {
    max-width: 150px;
    max-height: 50px; } }

.footer-widget {
  &__title {
    font-size: rem(20px);
    padding-bottom: rem(5px);
    color: #fff;
    position: relative;
    border-bottom: 1px solid rgba(#fff, 0.15);
    margin-bottom: rem(25px);
    &::after {
      position: absolute;
      content: '';
      bottom: 0;
      left: 0;
      width: 40px;
      height: 2px;
      @extend %base-color; } }
  &__list {
    a {
      &:hover {
        @extend %text--base; } } } }
.overview-item {
  padding: 15px 0;
  border-bottom: 1px solid rgba(#fff, 0.15);
  &:first-child {
    padding-top: 0; }
  &:last-child {
    padding-bottom: 0;
    border-bottom: none; }
  &__number {
    font-size: rem(32px);
    line-height: 1.3; } }
.social-link {
  li {
    padding: rem(3px) rem(7px);
    a {
      font-size: rem(18px);
      &:hover {
        i {
          @extend %text--base; } } } } }
/* footer section css end */
