/* job type section section css start */
.job-type-btn-area {
  padding: rem(15px) 0; }
.job-type-wrapper {
  display: none; }

.job-type-btn {
  position: relative;
  &::before {
    position: absolute;
    content: 'Click Me';
    top: -50px;
    left: 50%;
    @include transform(translateX(-50%));
    z-index: 1; }
  &::after {
    position: absolute;
    content: "\f309";
    font-family: 'Line Awesome Free';
    font-weight: 900;
    top: -35px;
    left: 50%;
    @include transform(translateX(-50%));
    z-index: 1;
    animation: upDown 2s infinite linear; } }
@include keyframes(upDown) {
  0% {
    top: -38px; }
  100% {
    top: -15px;
    opacity: 0; } }


.job-type-tabs {
  border-bottom: none;
  justify-content: center;
  .nav-item {
    width: 25%;
    @include media(991px) {
      width: auto; }
    .nav-link {
      @include border-radius(0);
      border: none;
      border-bottom: 2px solid transparent;
      @include border-radius(0 !important);
      background-color: transparent;
      color: #fff;
      display: flex;
      align-items: center;
      width: 100%;
      justify-content: center;
      i {
        font-size: rem(22px);
        line-height: 1;
        margin-right: 10px; }
      &.active {
        @extend %text--base;
        border-bottom-color: $base-color; } } } }

.job-cat-list {
  @extend %d-flex;
  margin: rem(-10px) 0;
  justify-content: space-between;
  li {
    width: 23%;
    padding: rem(10px) 0;
    border-bottom: 1px solid rgba(#fff, 0.1);
    @include media(991px) {
      width: 31%; }
    @include media(767px) {
      width: 48%; }
    @include media(480px) {
      font-size: rem(14px); }
    a {
      color: #fff;
      &:hover {
        @extend %text--base; } } } }
/* job type section section css end */

/* hot job section css start */
.short-job-card {
  padding: rem(10px);
  background-color: #fff;
  @extend %bs-8;
  box-shadow: 0 3px 10px rgba($base-color-two, 0.05);
  height: 100%;
  @extend %transition;
  border: 1px solid $border-color;
  &:hover {
     box-shadow: 0 5px 20px rgba($base-color-two, 0.08);
     @include transform(translateY(-3px)); }
  &__top {
    @extend %d-flex;
    @include media(767px) {
      justify-content: center;
      text-align: center; } }
  &__thumb {
    width: 70px;
    height: 70px;
    @extend %bs-5;
    overflow: hidden;
    box-shadow: 0 0 15px rgba(#000, 0.08);
    img {
      width: 100%;
      height: 100%;
      @extend %obj-fit; } }
  &__content {
    width: calc(100% - 100px);
    padding: 0 rem(15px);
    @include media(767px) {
      padding: 0;
      width: 100%;
      margin-top: rem(20px); }
    .caption {
      font-size: rem(14px);
      @include media(480px) {
        font-size: rem(12px); } } }
  &__title {
    font-size: rem(17px);
    @include media(480px) {
      font-size: rem(14px); } }
  &__action {
    width: 30px;
    text-align: right; } }
.bookmark-btn {
  background-color: transparent;
  padding: 0;
  font-size: rem(16px);
  color: lighten($para-color , 20%);
  line-height: 1;
  position: relative;
  .bookmarked-active {
    position: absolute;
    top: 5px;
    left: 0;
    @include transform(scale(0));
    color: $danger;
    @extend %transition; }
  .non-bookmark {
    @extend %transition; }
  &.active {
    .non-bookmark {
      opacity: 0; }
    .bookmarked-active {
      @include transform(scale(1));
      top: 0; } } }
/* hot job section css end */

/* feature section css start */
.feature-card {
  height: 100%;
  @include media(575px) {
    padding: rem(30px);
    background-color: rgba($base-color, 0.25);
    border: 1px solid rgba($base-color, 0.45);
    @include border-radius(10px);
    text-align: center;
    width: 80%;
    margin: 0 auto; }
  @include media(420px) {
    width: 100%;
    padding: rem(20px); }
  &__icon {
    position: relative;
    z-index: 1;
    width: 50px;
    height: 50px;
    @extend %base-color;
    color: #fff;
    display: inline-flex;
    align-items: center;
    justify-content: center;
    @extend %bs-5;
    i {
      font-size: rem(32px); } }
  &__content {
    margin-top: rem(20px); } }
/* feature section css end */

/* cta section css start */
.cta-title {
  font-size: rem(42px);
  @include media(991px) {
    font-size: rem(36px); }
  @include media(575px) {
    font-size: rem(32px); } }
/* cta section css end */

/* package section css start */
.package-details-wrapper {
  padding: rem(40px);
  height: 100%;
  background-color: lighten($base-color-two, 5%);
  @extend %bs-8; }

.package-card {
  padding: rem(25px);
  background-color: #fff;
  @extend %bs-8;
  box-shadow: 0 0 10px rgba($base-color-two, 0.08);
  @extend %transition;
  @include media(575px) {
    padding: rem(15px); }
  &:hover {
    box-shadow: 0 8px 25px rgba($base-color-two, 0.15);
    @include transform(translateY(-5px)); }
  &__top {
    @extend %d-flex;
    justify-content: center;
    .icon {
      width: 75px;
      height: 75px;
      display: flex;
      justify-content: center;
      align-items: center;
      @extend %base-color-two;
      @extend %bs-8;
      overflow: hidden;
      position: relative;
      &::before {
        position: absolute;
        content: '';
        top:  -30px;
        right: -30px;
        width: 60px;
        height: 60px;
        background-color: rgba(#fff, 0.2);
        @extend %bs-8; }
      &::after {
        position: absolute;
        content: '';
        bottom:  -30px;
        left: -30px;
        width: 60px;
        height: 60px;
        background-color: rgba(#fff, 0.2);
        @include border-radius(50%); }
      i {
        color: #fff;
        font-size: rem(42px);
        line-height: 1; } }
    .content {
      // width: calc(100% - 75px)
      padding-left: rem(20px); } }
  &__price {
    font-size: rem(24px);
    font-family: $heading-font;
    font-weight: 600;
    color: $heading-color;
    sub {
      font-size: 60%;
      color: {}
      font-weight: 400;
      color: #777;
      top: 0; } }
  &__content {
    margin-top: rem(25px);
    display: flex;
    justify-content: center; }
  &__feature-list {
    li + li {
      margin-top: rem(10px); }
    li {
      position: relative;
      padding-left: rem(23px);
      &::before {
        position: absolute;
        content: "\f00c";
        top: 0;
        left: 0;
        font-family: 'Line Awesome Free';
        font-weight: 900; } } }
  &.popular-package {
    @include transform(scale(1.1)); } }
/* package section css end */

/* brand section css start */
.brand-item {
  img {
    max-height: 30px; } }
/* brand section css end */

/* search result section css start */
.page-breadcum-menu-area {
  padding: rem(15px) 0;
  background-color: #fff;
  border-bottom: 1px solid $border-color; }
.page-breadcum-menu {
  li {
    padding: rem(3px) rem(10px);
    position: relative;
    font-size: rem(14px);
    &:last-child {
      &::after {
        display: none; } }
    &::after {
      position: absolute;
      content: '/';
      top: 50%;
      right: -3px;
      @include transform(translateY(-50%)); }
    a {
      @extend %text--base; } } }
.search-job-area {
  padding: rem(15px) 0; }

.job-card {
  padding: rem(20px) rem(25px);
  border: 1px solid $border-color;
  @extend %bs-5;
  font-size: rem(14px);
  background-color: #fff;
  @extend %transition;
  @include media(575px) {
    padding: rem(20px); }
  &:hover {
    box-shadow: 0 5px 15px rgba(#000, 0.05);
    @include transform(translateY(-3px)); }
  & + & {
    margin-top: rem(15px); }
  &.featured {
    background-color: #FFFDE8;
    border-color: #f7e311;
    position: relative;
    padding-top: rem(35px);
    &::after {
      position: absolute;
      content: 'Featured';
      top: 0;
      left: -8px;
      font-size: rem(12px);
      background-color: $danger;
      color: #fff;
      padding: rem(2px) rem(20px); }
    &::before {
      position: absolute;
      content: '';
      top: 14px;
      left: -18px;
      border-width: 10px 10px 10px 10px;
      border-style: solid;
      border-color: transparent darken($danger , 20%) transparent transparent; } }
  strong {
    font-weight: 500; }
  &__title {
    font-size: rem(20px); }
  &__top {
    .left {
      width: calc(100% - 15px); } }
  &__bookmark {
    width: 15px; }
  &__meta {
    margin: rem(-3px) rem(-7px);
    li {
      padding: rem(3px) rem(7px); } }
  &__description {
    width: 90%;
    @include media(575px) {
      width: 100%; } } }
/* search result section css end */

/* job details section css start */
.job-details {
  padding: rem(30px);
  border: 1px solid $border-color;
  @include media(575px) {
    padding: rem(20px); }
  &__title {
    font-size: rem(24px);
    @include media(575px) {
      font-size: rem(22px); }
    @include media(480px) {
      font-size: rem(20px); } }
  &__meta {
    margin: rem(-3px) rem(-12px);
    li {
      padding: rem(3px) rem(12px);
      font-size: rem(14px);
      position: relative;
      &:last-child {
        &::after {
          display: none; } }
      &::after {
        position: absolute;
        right: -2px;
        top: 3px;
        content: '|';
        color: #d4d4d4; } } }
  .bookmark-btn {
    font-size: rem(20px); } }
.fw-500 {
  font-weight: 500 !important; }
.job-alert-box {
  padding: rem(20px);
  border: 1px solid $border-color;
  @extend %bs-5;
  background-color: #f6fffd;
  &__icon {
    width: 50px;
    i {
      font-size: rem(42px);
      @extend %text--base; } }
  &__content {
    width: calc(100% - 50px);
    padding-left: 5px; } }

.job-alert-form {
  position: relative;
  &__btn {
    @extend %base-color;
    position: absolute;
    color: #fff;
    position: absolute;
    top: 4px;
    right: 5px;
    width: 35px;
    height: calc(100% - 8px); } }

.job-summary {
  background-color: #fff;
  @include border-radius(0 0 5px 5px);
  box-shadow: 0 2px 5px 0 rgba(#000, 0.05);
  &__title {
    padding: rem(8px) rem(20px);
    @extend %base-color-two;
    color: #fff;
    font-size: rem(20px);
    @include border-radius(5px 5px 0 0); }
  &__list {
    padding: rem(20px);
    li {
      font-size: rem(14px);
      padding: rem(8px) 0;
      border-bottom: 1px dashed $border-color;
      &:first-child {
        padding-top: 0; }
      &:last-child {
        padding-bottom: 0;
        border-bottom: none; } } } }
.job-details-sidebar {
  position: sticky;
  top: 30px; }
/* job details section css end */

/* blog section css start */
.blog-post {
  padding: rem(10px);
  background-color: #fff;
  border: 1px solid $border-color;
  @extend %bs-5;
  box-shadow: 0 0 10px rgba(#000, 0.08);
  &__date {
    position: absolute;
    left: 15px;
    bottom: 15px;
    background-color: #fff;
    padding: rem(2px) rem(15px);
    @include border-radius(5px);
    font-size: rem(14px);
    display: inline-flex;
    align-items: center;
    i {
      @extend %text--base;
      font-size: rem(18px);
      margin-right: 8px; } }
  &__thumb {
    height: 250px;
    position: relative;
    overflow: hidden; }
  &__title {
    font-size: rem(20px); }
  &__content {
    padding: rem(10px);
    margin-top: rem(15px); } }
blockquote {
  padding: rem(30px);
  @extend %base-color;
  color: #fff;
  margin: rem(20px) 0;
  font-size: rem(18px);
  font-style: italic; }
/* blog section css end */


/* candidate header section css start */
.candidate-header {
  padding: rem(70px) 0;
  @extend %base-color-two; }

.candidate {
  @extend %d-flex;
  align-items: center;
  &__thumb {
    width: 150px;
    height: 150px;
    @extend %bs-8;
    overflow: hidden;
    border: 1px solid $base-color;
    img {
      width: inherit;
      height: inherit;
      @extend %obj-fit; } }
  &__content {
    width: calc(100% - 150px);
    padding-left: rem(25px);
    @include media(480px) {
      width: 100%;
      padding-left: 0;
      margin-top: rem(20px); } }
  &__info-list {
    @extend %d-flex;
    align-items: center;
    margin: rem(-5px) rem(-10px);
    li {
      color: darken(#fff, 10%);
      padding: rem(5px) rem(10px);
      position: relative;
      i {
        font-size: rem(18px); } } } }
/* candidate header section css end */

/* candidate details section css start */
.experience-area {
  position: relative;
  &::after {
    position: absolute;
    content: '';
    top: rem(10px);
    left: rem(9px);
    width: 3px;
    height: calc(100% - 15px);
    background-color: transprent;
    border-left: 1px dashed rgba($base-color, 0.35); } }
.single-experience {
  padding-left: rem(30px);
  position: relative;
  & + & {
    margin-top: rem(30px); }
  &::before {
    position: absolute;
    content: '';
    top: rem(9px);
    left: rem(5px);
    width: rem(8px);
    height: rem(8px);
    @extend %base-color;
    @include border-radius(50%); }
  &::after {
    position: absolute;
    content: '';
    top: rem(4px);
    left: 0;
    width: rem(18px);
    height: rem(18px);
    @extend %base-color;
    @include border-radius(50%);
    opacity: 0.25; } }
.social-link-list {
  margin: rem(-3px) rem(-10px);
  li {
    padding: rem(3px) rem(10px); } }
/* candidate details section css end */

/* account section css start */
.account-section {
  position: relative;
  z-index: 1;
  &::before {
    @include positionTwo;
    background-color: rgba(#fff, 0.15);
    backdrop-filter: blur(20px);
    -webkit-backdrop-filter: blur(20px);
    z-index: -1; }
  div[class*="circle-"] {
    position: absolute;
    z-index: -2; }
  .circle-1 {
    top: 10%;
    left: 25%;
    width: 350px;
    height: 350px;
    @include border-radius(50%);
    background: linear-gradient(238.72deg, #16c79a 0%, #56124F 100%), linear-gradient(127.43deg, #00FFFF 0%, #FF4444 100%), radial-gradient(100.22% 100% at 70.57% 0%, #16c79a 0%, #28f7c3 100%), linear-gradient(127.43deg, #B7D500 0%, #3300FF 100%);
    background-blend-mode: hard-light, color-burn, color-dodge, normal; }
  .circle-2 {
    bottom: 10%;
    right: 25%;
    width: 200px;
    height: 200px;
    @include border-radius(50%);
    background: linear-gradient(238.72deg, #16c79a 0%, #56124F 100%), linear-gradient(127.43deg, #00FFFF 0%, #FF4444 100%), radial-gradient(100.22% 100% at 70.57% 0%, #16c79a 0%, #28f7c3 100%), linear-gradient(127.43deg, #B7D500 0%, #3300FF 100%);
    background-blend-mode: hard-light, color-burn, color-dodge, normal; } }

.account-form {
  background-color: #EBF5F5;
  border: 4px solid rgba($base-color, 0.25);
  box-shadow: 0 10px 25px rgba($base-color, 0.15);
  .title {
    font-size: rem(32px); }
  .subtitle {
    position: relative;
    &::before {
      position: absolute;
      content: '';
      top: 50%;
      left: -35px;
      width: 25px;
      height: 2px;
      margin-top: -1px;
      @extend %base-color; }
    &::after {
      position: absolute;
      content: '';
      top: 50%;
      right: -35px;
      width: 25px;
      height: 2px;
      margin-top: -1px;
      @extend %base-color; } } }
/* account section css end */
