
/* contact section css start */
.contact-form-area {
  background-color: #fff;
  padding: rem(30px);
  border: 1px solid $border-color;
  @extend %bs-5;
  box-shadow: 0 0 15px rgba($base-color-two, 0.05);
  .title {
    position: relative;
    padding-bottom: rem(5px);
    margin-bottom: rem(10px);
    &::after {
      position: absolute;
      content: '';
      bottom: 0;
      left: 0;
      width: 25px;
      height: 1px;
      @extend %base-color; } } }
.map-area {
  filter: grayscale(100%);
  background-color: #fff;
  &:hover {
    .map-address {
      opacity: 0;
      visibility: hidden; } }
  iframe {
    width: 100%;
    height: 615px;
    @include media(991px) {
      height: 500px; }
    @include media(767px) {
      height: 400px; } }
  .map-address {
    position: absolute;
    padding: rem(25px);
    @extend %base-color-two;
    color: #fff;
    font-size: rem(14px);
    width: 350px;
    @extend %transition;
    z-index: 1;
    @include media(480px) {
      width: 100%; }
    &::after {
      position: absolute;
      content: '';
      bottom: -18px;
      right: 0;
      border-width: 20px 20px 20px 20px;
      border-color: transparent $base-color-two  transparent transparent;
      border-style: solid;
      z-index: -1; } } }

.contact-item {
  @extend %d-flex;
  align-items: center;
  padding: rem(15px);
  background-color: #fff;
  box-shadow: 0 5px 10px rgba(#000, 0.1);
  @extend %bs-8;
  border: 1px solid $border-color;
  &__icon {
    width: 75px;
    height: 75px;
    @extend %base-color-two;
    color: #fff;
    display: flex;
    justify-content: center;
    align-items: center;
    @extend %bs-5;
    @include media(480px) {
      width: 55px;
      height: 55px; }
    i {
      font-size: rem(46px);
      @include media(480px) {
        font-size: rem(36px); } } }
  &__content {
    width: calc(100% - 75px);
    padding-left: rem(20px);
    @include media(480px) {
      width: calc(100% - 55px); } }
  a {
    color: $para-color;
    &:hover {
      @extend %text--base; } } }
/* contact section css end */
