/* dashboard section css start */
.d-widget {
  padding: rem(20px);
  background-color: #ffffff;
  overflow: hidden;
  position: relative;
  box-shadow: 0 5px 25px rgba(#000000, 0.1);
  @include transition(all 0.3s);
  @extend %bs-8;
  @media (max-width: 420px) {
    justify-content: center; }
  &:hover {
    box-shadow: 0 15px 35px rgba(#000000, 0.1);
    @include transform(translateY(-5px)); }
  &::before {
    position: absolute;
    content: '';
    bottom: -9px;
    left: -26px;
    width: 60px;
    height: 30px;
    background-color: $base-color;
    @include transform(rotate(45deg)); }
  &__content {
    width: calc(100% - 75px);
    padding-right: rem(20px);
    @media (max-width: 420px) {
      width: 100%;
      padding-right: 0;
      order: 2;
      text-align: center;
      margin-top: 25px; }
    .d-number {
      font-size: rem(32px);
      font-weight: 600; }
    .caption {
      margin-top: 5px; } }
  &__icon {
    width: 75px;
    text-align: end;
    @media (max-width: 420px) {
      order: 1;
      text-align: center; }
    i {
      font-size: 42px;
      @extend %text--base; } }
  &__btn {
    background-color: $base-color;
    color: #ffffff;
    padding: 2px 10px;
    font-size: 12px;
    @include border-radius(3px);
    &:hover {
      color: #ffffff; } } }

.profile-thumb-wrapper {
  background-color: #fff;
  padding: rem(20px);
  @extend %bs-8;
  box-shadow: 0 0 10px rgba(#000, 0.05); }
.profile-thumb {
  display: flex;
  align-items: center;
  @include media(520px) {
    flex-wrap: wrap; } }
.avatar-edit {
  @include media(520px) {
    width: 100%;
    padding-left: 0 !important;
    margin-top: rem(15px); } }
.avatar-preview {
  width: rem(180px);
  height: rem(180px);
  @include border-radius(15px);
  display: inline-flex;
  .profilePicPreview {
    width: rem(180px);
    height: rem(180px);
    @include border-radius(15px);
    display: block;
    background-size: cover;
    background-position: center; } }
.profilePicUpload {
  font-size: 0;
  opacity: 0;
  width: 0; }


/* dashboard section css end */
