/* sidebar css start */
.sidebar {
  padding-left: 30px;
  position: sticky;
  top: 30px;
  @media (max-width: 991px) {
    padding-left: 0; }
  .widget + .widget {
    margin-top: 30px;
    @media (max-width: 767px) {
      margin-top: 30px; } }
  .widget {
    padding: 20px;
    border: 1px solid $border-color;
    background-color: #fff;
    @extend %bs-8;
    .widget__title {
      position: relative;
      padding-bottom: 8px;
      margin-bottom: 25px;
      &::after {
        position: absolute;
        content: '';
        left: 0;
        bottom: 0;
        width: 20px;
        height: 2px;
        background-color: $base-color; } } }
  .small-post-list {
    .small-post {
      @include d-flex;
      padding: 20px 0;
      border-bottom: 1px solid #e5e5e5;
      &:first-child {
        padding-top: 0; }
      &:last-child {
        padding-bottom: 0;
        border-bottom: none; }
      &__thumb {
        width: 58px;
        height: 58px;
        img {
          width: inherit;
          height: inherit;
          @extend %obj-fit; } }
      &__content {
        width: calc(100% - 58px);
        padding-left: 20px; }
      &__title {
        a {
          font-size: rem(16px);
          font-weight: 600; } } } } }
/* sidebar css end */


/* action-sidebar css start */
.action-sidebar {
  padding: rem(20px) rem(15px);
  background-color: #fff;
  @extend %transition;
  // border: 1px solid lighten($border-color , 10%)
  // box-shadow: 0 0 5px rgba($base-color-two, 0.15)
  @extend %bs-8;
  @include media(991px) {
    padding: 30px;
    width: 350px;
    position: fixed;
    top: 100px;
    left: -105%;
    z-index: 99999;
    max-width: 100%;
    box-shadow: 5px 5px 10px rgba(#000, 0.15);
    max-height: calc(100vh - 75px);
    overflow: auto;
    scrollbar-width: thin;
    scrollbar-color: darkgrey #e7e7e7;
    &::-webkit-scrollbar {
      width: 5px; }
    &::-webkit-scrollbar-track {
      box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3); }
    &::-webkit-scrollbar-thumb {
      background-color: darkgrey;
      @include border-radius(999px); }
    &.active {
      left: 0; } } }
.action-widget {
  padding: 20px 0;
  border-bottom: 1px solid lighten($border-color , 5%);
  &:last-child {
    padding-bottom: 0;
    border-bottom: none; }
  &__title {
    font-size: rem(16px);
    color: $heading-color;
    // padding-bottom: rem(5px)
    // border-bottom: 1px solid lighten($border-color, 8%)
    position: relative;
    padding-right: 20px;
    cursor: pointer;
    &::after {
      position: absolute;
      content: "\f107";
      font-family: 'Line Awesome Free';
      font-weight: 900;
      top: 0;
      right: 0;
      font-size: rem(14px); } }
  &__body {
    margin-top: rem(15px);
    &.scroll--active {
      max-height: 200px;
      overflow: auto;
      scrollbar-width: thin;
      scrollbar-color: darkgrey #e7e7e7;
      &::-webkit-scrollbar {
        width: 5px; }
      &::-webkit-scrollbar-track {
        box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3); }
      &::-webkit-scrollbar-thumb {
        background-color: darkgrey;
        @include border-radius(999px); } } } }
.search-form-inline {
  position: relative;
  input {
    padding-left: 10px;
    padding-right: 30px; }
  &__btn {
    position: absolute;
    top: 50%;
    right: 5px;
    @include transform(translateY(-50%));
    background-color: transparent; } }
.action-widget {
  .form-check + .form-check {
    margin-top: rem(5px); } }
.action-sidebar-close {
  width: 30px;
  height: 30px;
  background-color: $base-color;
  color: #fff;
  position: absolute;
  top: 0;
  right: 0;
  display: none;
  @include media(991px) {
    display: inline-block; } }
.action-sidebar-open {
  padding: rem(8px) rem(25px);
  background-color: $base-color;
  color: #fff;
  align-items: center;
  justify-content: center;
  @include border-radius(3px);
  display: none;
  @include media(991px) {
    display: inline-flex; }
  i {
    font-size: rem(22px);
    margin-right: 5px; } }
/* action-sidebar css end */


/* candidate-sidebar css start */
.candidate-sidebar-area {
  position: sticky;
  top: 30px; }
.candidate-sidebar {
  box-shadow: 0 0 10px rgba(#000, 0.05);
  &__title {
    @extend %base-color-two;
    color: #fff;
    padding: rem(12px) rem(20px);
    font-size: rem(20px);
    @include border-radius(5px 5px 0 0); }
  &__body {
    background-color: #fff;
    padding: rem(20px);
    @include border-radius(0 0 8px 8px); } }
/* candidate-sidebar css end */

/* dashboard-sidebar css start */
.dashboard-sidebar {
  position: sticky;
  top: 30px;
  padding: rem(20px);
  background-color: #fff;
  @extend %bs-8;
  box-shadow: 0 0 10px rgba(#000, 0.05);
  &__thumb {
    @extend %bs-5;
    overflow: hidden; }
  &__menu {
    li {
      &:last-child {
        a {
          border-bottom: none;
          padding-bottom: 0; } }
      &.active {
        a {
          @extend %text--base; } }
      a {
        display: flex;
        align-items: center;
        padding: rem(10px) 0;
        color: $para-color;
        border-bottom: 1px dashed $border-color;
        &:hover {
          @extend %text--base; }
        i {
          font-size: rem(22px);
          margin-right: rem(8px); } } } } }
/* dashboard-sidebar css end */
