/* hero section css start */
.hero {
  padding: rem(150px) 0;
  position: relative;
  z-index: 1;
  @include media(1199px) {
    padding: rem(120px) 0; }
  @include media(575px) {
    padding: rem(100px) 0; }
  &::before {
    @include positionTwo;
    background: #bdc3c7;
    background: -webkit-linear-gradient(to top, rgba($base-color-two, 0.98), rgba($base-color-two, 0.45));
    background: linear-gradient(to top, rgba($base-color-two, 0.98), rgba($base-color-two, 0.45));
    z-index: -1; }
  &__title {
    font-size: rem(56px);
    @include media(1199px) {
      font-size: rem(44px); }
    @include media(575px) {
      font-size: rem(36px); } }
  &__form {
    @extend %d-flex;
    align-items: center;
    padding: rem(10px) rem(15px);
    @extend %bs-8;
    background-color: #fff;
    justify-content: space-between;
    @include media(480px) {
      justify-content: center; }
    .input-field {
      display: flex;
      align-items: center;
      width: calc((100% / 2) - 70px);
      border-bottom: 1px solid $border-color;
      @include media(480px) {
        width: 50%; }
      span {
        font-weight: 500; }
      .form--control {
        flex-grow: 1; } }
    .form--control {
      background-color: transparent;
      border: none;
      &:focus {
        box-shadow: none; } } }
  &__form-btn {
    width: 80px;
    @extend %base-color;
    color: #fff;
    width: 100px;
    padding: rem(12px);
    @extend %bs-5;
    @include media(480px) {
      margin-top: rem(15px);
      width: 100%; } } }
/* hero section css end */

/* inner hero css start */
.inner-hero {
  padding-top: rem(50px);
  padding-bottom: rem(50px);
  position: relative;
  z-index: 1;
  &::after {
    @include positionTwo;
    @extend %base-color-two;
    opacity: 0.65;
    z-index: -1; } }

.page-breadcrumb {
  @include d-flex;
  margin-top: rem(5px);
  li {
    color: rgba(#ffffff, 0.8);
    text-transform: capitalize;
    &::after {
      content: '-';
      color: #ffffff;
      margin: 0 rem(5px); }
    &:first-child {
      &::before {
        content: "\f015";
        font-family: "Font Awesome 5 Free";
        font-weight: 900;
        color: $base-color;
        margin-right: rem(6px); } }
    &:last-child {
      &::after {
        display: none; } }
    a {
      color: #ffffff;
      text-transform: capitalize;
      &:hover {
        color: $base-color; } } } }
/* inner hero css end */
